// all imports

@import url('https://fonts.cdnfonts.com/css/gilroy-bold');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

// variables

$blackColor: black;
$whiteColor: white;

$biggestFont: 36px;
$bigFont: 24px;
$midleFont: 16px;
$smallFont: 12px;

// main style

body {
	margin: 0;
	font-family: 'Montserrat', sans-serif;
	font-size: $midleFont;
	line-height: 1.6;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// header

.header {
	background: $blackColor;
	padding: 60px 0;
	position: relative;
	img {
		margin: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-right: -50%;
		transform: translate(-50%, -50%);
	}
}

.nav {
	a {
		font-weight: 400;
		font-size: $midleFont;
		opacity: 0.5;
		color: $whiteColor;
	}
	a:hover {
		opacity: 1;
		color: $whiteColor;
	}
}

.footer {
	background: #2b8401;
}

// cars

.cars__suptitle {
	font-weight: 400;
	font-size: $bigFont;
	color: $blackColor;
	opacity: 0.6;
}

.cars__title {
	font-style: normal;
	font-weight: 400;
	font-size: $biggestFont;
}

.cars__buttons {
	button {
		font-weight: 400;
		font-size: $bigFont;
		line-height: 23px;
		color: $whiteColor;
	}
}
.card__name {
	font-style: normal;
	font-weight: 400;
	font-size: $biggestFont;
	line-height: 35px;
}

.card__price {
	font-style: normal;
	font-weight: 400;
	font-size: $biggestFont;
	line-height: 35px;
	color: $blackColor;
	opacity: 0.6;
}

.card__desk {
	font-weight: 400;
	font-size: $smallFont;
	color: $blackColor;
	opacity: 0.6;
}

// modal

.carModalBack {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #2222227e;
	z-index: 9999;
	overflow-x: auto;
}

.carModal {
	border-radius: 9px;
	position: relative;
	width: 80%;
	margin: 30px auto;
	background-color: white;
	padding: 40px 30px;
}

// tuning

.tuning__title {
	font-size: $biggestFont;
}

.tuning {
	font-size: $bigFont;
	img {
		width: 30px;
	}
	.tuning__name {
		font-size: $midleFont;
		span {
			color: #2b8401;
		}
	}
}

.carOrderBack {
	position: fixed;
	overflow-x: auto;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #2222227e;
	z-index: 9999;
	.carOrder {
		border-radius: 9px;
		position: relative;
		width: 80%;
		margin: 30px auto;
		background-color: white;
		padding: 40px 30px;
	}
}

.order__color {
	color: #2b8401;
}

.mobileversion__show {
	display: none;
}

.fixLink {
	text-decoration: none;
	color: $blackColor;
	opacity: 0.6;
}

.about__car {
	h2 {
		font-size: 32px;
		line-height: 160%;
		color: #2b3244;
		margin-bottom: 20px;
		b {
			color: #2b8401;
		}
	}

	p {
		font-size: 16px;
		line-height: 160%;
		color: #2b3244;
		text-align: right;
	}
	img {
		width: 100%;
		border-radius: 12px;
		height: 640px;
		object-fit: cover;
		margin: 20px 0;
	}
}

// Main page

.main {
	.main__suptitle {
		font-weight: 500;
		font-size: $midleFont;
		line-height: 20px;
	}

	.main__title {
		font-style: normal;
		font-weight: 600;
		font-size: 40px;
		line-height: 44px;

		b {
			color: #2b8401;
			font-weight: 600;
		}
	}
	.main__subtitle-line {
		position: relative;
		font-weight: 400;
	}

	.main__subtitle-line::after {
		content: '';
		position: absolute;
		left: -45px;
		width: 35px;
		height: 1px;
		background: #9a9a9a;
		border-radius: 20px;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	a {
		text-decoration: none;
		transition: all 0.4s ease;
	}
	.aboutCar {
		margin-top: 40px;
		border-bottom: 2px solid #efefef;
		padding-bottom: 16px;
		.aboutCar__name {
			font-size: 24px;
			line-height: 26px;
			color: #2b3244;
			margin-bottom: 10px;
		}
		.aboutCar__title {
			font-size: 12px;
			line-height: 14px;
			color: #555555;
			margin-bottom: 6px;
		}

		.aboutCar__price {
			font-size: 16px;
			line-height: 17px;
			color: #2b3244;
		}
		.aboutCar__more {
			font-size: 16px;
			line-height: 17px;
			color: #2b3244;
			position: relative;
			padding-right: 20px;
		}

		.aboutCar__more::after {
			position: absolute;
			content: '';
			right: 0;
			width: 10px;
			height: 10px;
			background-image: url(https://rucars.ru/img/more.svg);
			background-position: center center;
			background-size: cover;
			top: 0;
			bottom: 0;
			margin: auto;
		}
	}

	.social {
		color: #98a3b4;
		padding: 22px 130px 22px 100%;
		background-image: url(https://rucars.ru/img/back2.jpg);
		background-position: center right;
		background-repeat: no-repeat;
		background-size: contain;
		position: relative;
		width: max-content;
		margin: 65px 0 0 -100%;
		background-color: #f5f5f5;
	}
	.social__title {
		font-size: $midleFont;
	}
}

.comment {
	img {
		width: 100px;
		border-radius: 20px;
	}
	.comment__ava {
		display: flex;
	}

	.comment__name {
		font-weight: 500;
	}

	.comment__status {
	}

	.comment__review {
		background-color: #e5e5e5;
		border-radius: 15px;
		padding: 25px;
	}
}

.work {
	.card {
		background-color: #f5f5f5;
		img {
			height: 500px;
			margin: 20px 0 30px;
		}

		.img-lg {
			width: 220px;
			height: 180px;
		}

		.card-body {
			background: #f5f5f5;
			border-radius: 12px;
			padding: 38px 35px 0;
			height: 450px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			transition: all 0.4s ease;
		}
		.card-title {
			font-size: 16px;
			line-height: 140%;
			text-align: center;
			color: #2b3244;
		}
		.button {
			font-size: 12px;
			line-height: 14px;
			color: #ffffff;
			background: #2b8401;
			border-radius: 0px 0px 5px 5px;
			padding: 17px;
			display: block;
			text-align: center;
			margin: -5px -36px;
		}
	}
}

.quest {
	background: #f5f5f5;
	border-radius: 12px;
	padding: 40px;
	display: flex;
	justify-content: space-between;
	margin-top: 60px;
	h2 {
		font-size: 22px;
		line-height: 140%;
		color: #2b3244;
		margin-bottom: 15px;
	}
	p {
		font-size: 20px;
		line-height: 160%;
		color: #2b3244;
	}
	.number {
		font-family: 'stol-reg';
		font-size: 28px;
		line-height: 34px;
		text-align: right;
		color: #2b3244;
		display: block;
	}
	.quest-text {
		font-size: 12px;
		line-height: 14px;
		color: #9a9a9a;
		text-align: end;
	}
}
@media (max-width: 1400px) {
	/*
  Стили для устройств, у которых ширина viewport меньше или равно 1400 пикселей,
  но больше 990 пикселей.
  Эти стили будут использованы для планшетов и ноутбуков с низким разрешением
  */
}

@media (max-width: 990px) {
	/*
  Стили для устройств, у которых ширина viewport меньше или равно 990 пикселей,
  но больше 770 пикселей.
  Эти стили подойдут для некоторых мобильных устройств и планшетов
  */
}

@media (max-width: 770px) {
	/*
  Стили для устройств, у которых ширина viewport меньше или равно 770 пикселей.
  Это множество мобильных устройств
  */

	.mobileversion__none {
		display: none;
	}

	.mobileversion__show {
		display: block;
	}

	.card__name {
		font-size: 18px;
	}
	.card__price {
		font-size: 16px;
	}
	.cars__suptitle {
		font-weight: 400;
		font-size: 16px;
	}

	.none {
		display: none;
	}
	.tuning__title {
		font-size: 18px;
	}
	.tuning {
		font-size: 16px;
		img {
			width: 30px;
			margin-right: 5px;
		}
		.tuning__name {
			font-size: 12px;
			span {
				color: #2b8401;
			}
		}
	}
	.quest {
		h2 {
			font-size: 20px;
		}
		p {
			font-size: 14px;
		}
		.number {
			font-size: 20px;
		}
		.quest-text {
			font-size: 10px;
		}
	}
	.about__car {
		h2 {
			font-size: 20px;
		}

		p {
			font-size: 12px;
		}
		img {
			width: 100%;
			height: 400px;
		}
	}
}
